<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">资讯管理</el-row>
    <el-row type="flex" justify="end" style="margin-top: 10px;">
      <el-button plain size="small">
        <router-link :to="{ path: '/information/detail', query: { pageState: 1 }}"> 添加资讯</router-link>
      </el-button>
    </el-row>
    <!-- 资讯列表 -->
    <el-row style="margin-top: 30px;">
      <el-table :data="infomationList"
                stripe
                style="width: 100%"
                @row-dblclick="dbClick">
        <el-table-column label="图片"
                        min-width="150">
          <template slot-scope="scope">
            <img :src="scope.row.image && scope.row.image.medium" style="width: 40px; height: 40px;background-color: #eee;">
          </template>
        </el-table-column>
        <el-table-column prop="title"
                        label="标题"
                        min-width="150">
        </el-table-column>
        <el-table-column prop="desc"
                        label="描述"
                        min-width="300"
                        show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="public_at"
                        label="发布时间"
                        min-width="150"
                        show-overflow-tooltip>
        </el-table-column>
        <el-table-column label="类别"
                        min-width="150">
            <template slot-scope="scope">
              <span>{{getPageType(scope.row.link_type)}}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                        min-width="100">
          <template slot-scope="scope">
            <el-button @click="editHandleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button  @click="deleteHandleClick(scope.row)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-row style="margin-top:30px;"  type="flex" justify="end">
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="curPage"
                     :page-sizes="[10, 20, 30, 40]"
                     :page-size="10"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-row>
  </div>
</template>
<script>

export default {
  data () {
    return {
      infomationList: [],
      curPage: 1,
      pageSize: 20,
      total: 0
    }
  },

  components: {
  },

  methods: {
    // 获取信息列表
    queryInfoList () {
      let params = {
        page: this.curPage,
        per: this.pageSize
      }
      this.$http.informationList(params, 'get').then((res) => {
        let result = res.data.success
        this.infomationList = result.informations || []
        this.curPage = result.page.current_page
        this.pageSize = result.page.per
        this.total = result.page.total_count
      })
    },
    // page大小改变
    handleSizeChange (page) {
      console.log('page大小改变', page)
      this.pageSize = page
    },

    // 当前page改变
    handleCurrentChange (page) {
      console.log('当前page改变', page)
      this.curPage = page
    },

    // 编辑操作
    editHandleClick (row) {
      this.$router.push({
        path: '/information/detail',
        query: {
          pageState: 2,
          information_id: row.id
        }
      })
    },

    dbClick (row) {
      this.$router.push({
        path: '/information/detail',
        query: {
          pageState: 3,
          information_id: row.id
        }
      })
    },

    // 删除操作
    deleteHandleClick (row) {
      this.$confirm('确定删除？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          information_id: row.id
        }
        this.$http.deteleInfomation(params, 'delete').then(res => {
          let result = res.data.success
          if (result) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            setTimeout(this.queryInfoList, 300)
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },

    getPageType (linkType) {
      let name = ''
      switch (linkType) {
        case 'page':
          name = '自定义页面'
          break
        case 'article':
          name = '链接公众号文章'
          break
      }
      return name
    }
  },
  created () {
    this.queryInfoList()
  }
}
</script>

